import { ErrorHandler, NgModule } from '@angular/core';
import { Http, HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
// import { Camera } from '@ionic-native/camera';
// import { GoogleMaps } from '@ionic-native/google-maps';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { NativePageTransitions } from '@ionic-native/native-page-transitions';

import { Notes } from '../providers/notes/notes';
import { Lignes } from '../providers/lignes/lignes';
import { Type_Notes } from '../providers/type-note/type-note';
import { Api } from '../providers/api/api';
import { Authentification } from '../providers/authentification/authentification';
import { MyApp } from './app.component';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/configuration/configuration.module#ConfigurationPageModule', name: 'ConfigurationPage', segment: 'configuration', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/liste-note/liste-note.module#ListeNotePageModule', name: 'ListeNotePage', segment: 'liste-note', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-note/single-note.module#SingleNotePageModule', name: 'SingleNotePage', segment: 'single-note', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-ligne/single-ligne.module#SingleLignePageModule', name: 'SingleLignePage', segment: 'single-ligne', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
  ],
	exports: [
	],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Notes,
		Lignes,
		Type_Notes,
		Authentification,
    // Camera,
    // GoogleMaps,
    SplashScreen,
    StatusBar,
		NativePageTransitions,
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



